@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.qodef-comment-holder {
    @include qodefRelativeHolderLayout(top);
	
	.qodef-comment-holder-inner {
		@include qodefRelativeHolderLayout(top);
		margin: 49px 0 0;
	}

    .qodef-comment {

        &.qodef-pingback-comment {

            .qodef-comment-text {
                padding: 0;
            }
        }
    }

    .qodef-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include qodefRelativeHolderLayout();
            margin: 65px 0 0;
            padding: 0;
	        border: 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
        }

        .children {
            padding: 0 0 0 80px;
        }
    }

    .qodef-comments-title {
        margin-bottom: 52px;

        > * {
            margin: 0;
        }
    }

    .qodef-comment-image {
        position: relative;
        top: 0;
        left: 0;
        width: 84px;
        display: block;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .qodef-comment-text {
        padding: 0 0 0 128px;
        min-height: 84px;
        
	    .qodef-comment-heading {
		    @include qodefFlexBoxLayout(relative, flex-start);
		    
		    > * {
			    width: 100%;
			    margin: 0;
		    }
	    }
	
	    .qodef-comment-actions {
		    text-align: right;
		    white-space: nowrap;
		    
		    > * {
			    font-weight: 500;
			    margin-right: 10px;
			    
			    &:last-child {
				    margin-right: 0;
			    }
		    }
	    }

        .qodef-text-holder p {
            margin: 9px 0 0;
        }
	    
	    .qodef-comment-date {
		    margin: 10px 0 0;
	    }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 18px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
	        
	        &:hover {
		        color: #fff;
		        text-decoration: underline;
	        }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
	
	.comment-respond {
		background-color: $first-main-color;
		padding: 20px;
	}
	
	.comment-reply-title {
		margin: 0 0 8px;
		color: #fff;
	}
	
	.qodef-comment-notes {
		display: block;
		max-width: 80%;
		margin: 0 0 25px;
		color: #fff;
		
		@include ipad-landscape {
			max-width: 100%;
		}
	}
	
	.logged-in-as {
		margin: 0 0 25px;
		color: #fff;
		
		a {
			color: inherit;
			
			&:hover {
				color: rgba(#fff, .75);
			}
		}
	}
	
	.qodef-comment-icon {
		position: absolute;
		top: 17px;
		left: 23px;
		font-size: 14px;
		color: #fff;
		z-index: 2;
	}
	
	.comment-form-cookies-consent {
		@include qodefFlexBoxLayout(relative, flex-start);
		margin: 4px 0 8px;
		color: #fff;
		
		input {
			position: relative;
			top: 3px;
			
			@include ipad-landscape {
				margin: 3px 10px 3px 0;
			}
		}
	}
	
	#respond {
		
		textarea,
		input[type=text] {
			padding-left: 47px;
			background-color: rgba(#fff, .25);
			color: #fff;
			
			&:focus {
				color: #fff;
			}
		}
	}
	
	#submit_comment {
		@include qodefButtonDefaultStyle();
		@include qodefButtonSize(huge);
		color: $default-heading-color;
		background-color: #fff;
		border: 0;
		cursor: pointer;
		border-radius: 0;
		-webkit-border-radius:0;
		-webkit-appearance: none;
		
		&:hover {
			color: #fff;
			background-color: $solid-button-hover-bg-color;
		}
	}
}

.qodef-comment-form {
    @include qodefRelativeHolderLayout(top);
	margin: 85px 0 0;
	
	.comment-respond {
		@include qodefRelativeHolderLayout(top);
		padding: 44px 40px 55px;
		background-color: $first-main-color;
		clear: both;
		box-sizing: border-box;
		
		@include ipad-landscape {
			padding: 44px 30px 55px;
		}
		
		@include phone-landscape {
			padding: 44px 20px 55px;
		}
	}

    .comment-reply-title {
        margin: 0 0 8px;
	    color: #fff;
    }
	
	.qodef-comment-notes {
		display: block;
		max-width: 80%;
		margin: 0;
		padding-bottom: 7px;
		color: #fff;
	}

    .logged-in-as {
        margin: 0 0 25px;
	    color: #fff;
	    
	    a {
		    color: inherit;
		    
		    &:hover {
			    color: rgba(#fff, .75);
		    }
	    }
    }
	
	.qodef-comment-icon {
		position: absolute;
		top: 17px;
		left: 23px;
		font-size: 14px;
		color: #fff;
		z-index: 2;
	}
	
	.qodef-comment-form-inner {
		
		.comment-form-cookies-consent {
			@include qodefFlexBoxLayout(relative, flex-start);
			margin: 15px 0 23px;
			color: #fff;
			
			input {
				position: relative;
				top: 3px;
				
				@include ipad-landscape {
					margin: 3px 10px 3px 0;
				}
			}
		}
	}
	
	#respond {
		
		textarea,
		input[type=text] {
			padding-left: 47px;
            background-color: rgba(#fff, .25);
            color: #fff;
            
            &:focus {
                color: #fff;
            }
		}
	}
	
	#submit_comment {
		@include qodefButtonDefaultStyle();
		@include qodefButtonSize(huge);
		color: $default-heading-color;
		background-color: #fff;
		font-family: $default-text-font;
		border: 0;
		cursor: pointer;
		border-radius: 0;
		-webkit-border-radius:0;
		-webkit-appearance: none;
		
		&:hover {
			color: #fff;
			background-color: $solid-button-hover-bg-color;
		}
	}
}

#review_form #respond {
	
	textarea,
	input[type=text],
	input[type=email] {
		background-color: rgba(#fff, .25);
		color: #fff;
	}
}

@include ipad-portrait {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
	
	    .qodef-comment-text {
		    padding-left: 108px;
	    }
    }
}

@include phone-landscape {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0;
            }
        }
    }
}

@include phone-portrait {

    .qodef-comment-holder {

        .qodef-comment-image {
            float: none;
        }

        .qodef-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
	        
	        .qodef-comment-heading {
		        flex-wrap: wrap;
	        }
	        
	        .qodef-comment-actions {
		        margin-top: 9px;
		        text-align: inherit;
		        white-space: inherit;
	        }
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.qodef-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;
	    color: #fff;
	    background-color: rgba(#fff, .25);
	
	    &:focus {
		    color: #fff;
	    }

        footer & {
            margin: 0;
        }
    }
}

