/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.qodef-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.qodef-slider-is-moving {
        
        .owl-item {
        
            .qodef-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.qodef-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                @include qodefTransform(translateY(-32px)); // 32 is is half height of pagination control + top margin
            }
        }
    }

    &.qodef-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: calc(50% - 34px);
            width: 55px;
            height: 55px;
            padding: 1px 0 0;
            margin: 0;
            font-size: 35px;
            line-height: 50px;
            text-align: center;
            color: $first-main-color;
            background-color: #fff;
            border: 0;
            border-radius: 100%;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;
	        box-sizing: border-box;
            @include qodefTransition(color .4s ease-in-out, background-color .2s ease-in-out);

            &:hover {
	            color: #fff;
	            background-color: $second-main-color;
            }

            > span {
                display: block;
	            line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 15px;
	        padding-left: 4px;
        }

        .owl-next {
            right: 15px;
	        padding-right: 4px;
        }
    }

    .owl-dots {
        text-align: center;
        margin: 27px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 8px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 12px;
                height: 12px;
                border-radius: 3em;
                background-color: rgba($first-main-color, .5);
                box-sizing: border-box;
                @include qodefTransition(background-color .2s ease-out);
            }

            &:hover,
            &.active {

                span {
                    background-color: $first-main-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .qodef-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .qodef-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include qodefTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.qodef-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .qodef-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
	
    .qodef-wrapper {
        padding: 0 !important;
    }

    .qodef-content {
        height: 100vh;
    }

    &.admin-bar {
        .qodef-content {
            height: calc(100vh - 32px);
        }
    }

    .qodef-content-inner {
        @include qodefTableLayout();
    }

    .qodef-page-not-found {
        @include qodefTableCellLayout();
        padding: 0 20%;
        text-align: center;
	    
	    .qodef-404-title-image {
		    margin-bottom: 15px;
	    }

        .qodef-404-title {
            margin: 0;
        }

        .qodef-404-text {
            margin: 14px auto 0;
	        float: none;
	        font-size: 17px;
        }

        .qodef-btn {
            margin: 30px 0 0;

            &.qodef-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }
	
	@include laptop-landscape-mac {
		
		.qodef-page-not-found {
			padding-top: 18px;
		}
	}

    @include ipad-landscape {
	    
        .qodef-content {
	        @include qodefFlexBoxLayout(relative, center);
            height: 100%;
            min-height: 100vh;
        }

        &.admin-bar {
	        
            .qodef-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
	
	    .qodef-page-not-found {
		    padding: 90px 40px 20px;
	    }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#qodef-back-to-top {
    position: fixed;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        @include qodefTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include qodefTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 60px;
        height: 60px;
        line-height: 60px;
        border-radius: 100%;
        font-size: 13px;
        font-weight: 600;
        letter-spacing: 1px;
        text-align: center;
        color: $first-main-color;
        background-color: #fff; 
        box-shadow: 4px 6px 15px 0px rgba(0, 0, 0, 0.08);
        overflow: hidden;

        @include qodefTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

        &:hover {
            color: #fff;
            background-color: $first-main-color;
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
