/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.qodef-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {

        &:last-child {
            margin: 0 !important;
        }
	
	    &.widget_text {
		
		    .qodef-widget-title {
			    margin-bottom: 23px;
		    }
	    }
	}
}

.wpb_widgetised_column {
    position: relative;

    .widget {
	
	    &:last-child {
		    margin: 0 !important;
	    }
    }
}
/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {
	margin: 0 0 35px;
	
	.qodef-widget-title {
		margin: 0 0 14px;
		
		a {
			color: inherit;
		}
	}
	
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		
		li {
			margin: 0 0 10px;
			
			&:last-child {
				margin: 0;
			}
		}
	}

    /*
    *   Specific styles
    */
    &.widget_text {

        img {
            display: block;
        }
    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: #fff;
            background-color: $first_main_color;

            a {
                color: inherit;
            }
        }
    }

    &.widget_rss {

        .qodef-widget-title {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.widget_search {
	    
	    .qodef-widget-title {
		    margin-bottom: 32px;
	    }

        form {
            @include qodefRelativeHolderLayout();
            clear: both;
        }

        input,
        button {
            position: relative;
            display: inline-block;
            vertical-align: top;
            float: left;
            height: 50px;
            font-family: inherit;
            font-size: inherit;
            line-height: 30px;
	        font-weight: inherit;
            border: 0;
            border-radius: 0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include qodefTransition(color .2s ease-out);
        }

        input {
            width: calc(100% - 50px);
            padding: 9px 0 11px 21px;
	        font-family: $default-text-font;
	        font-size: 14px;
	        font-weight: 400;
	        color: $additional-text-color;
	        background-color: $additional-background-color;
	
	        @include qodefPlaceholder {
		        color: $additional-text-color;
	        }
        }

        button {
            width: 50px;
            padding: 10px 0;
	        text-align: center;
            color: #fff;
	        background-color: $first-main-color;
            cursor: pointer;
            @include qodefTransition(background-color .2s ease-out);

            &:hover {
	            background-color: $second-main-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }
	
	&.qodef-tours-main-search-filters {
		margin-bottom: 30px;
	}

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_recent_entries,
    &.widget_nav_menu {

        ul {

            li {
                margin: 0;
                padding: 6px 0;

                &:last-child {
                    border: none;
                }
                
                a {
                    color: $default-text-color;
                    
                    &:hover {
                        color: $first-main-color;
                    }
                }

                ul {
                    margin: 6px 0 0;
                    padding: 0 0 0 10px;

                    li {

                        &:last-child {
                            padding: 6px 0 0;
                        }
                    }
                }
            }
        }
    }
	
	&.widget_recent_entries {

        ul {

            li {

                > span {
                    display: block;
                }
            }
        }
    }
	
	&.widget_categories {
		
		ul {
			
			li {
				
				a {
					font-weight: 600;
					color: $default-heading-color;
				}
			}
		}
	}
	
    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            width: 100%;
            max-width: 100%;
            height: 50px;
            padding: 0 20px;
	        margin-top: 15px;
	        font-family: inherit;
	        font-size: inherit;
	        line-height: 50px;
	        font-style: inherit;
	        font-weight: inherit;
            color: $additional-text-color;
            background-color: $additional-background-color;
            border: 0;
	        border-radius: 0;
            outline: none;
            box-sizing: border-box;
        }
    }

    &.posts_holder {

        ul {
            
            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_tag_cloud {

        .qodef-widget-title {
            margin-bottom: 29px;
        }
    
        a {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 3px 6px 0;
            padding: 10px 24px;
            @include qodefHeadingStyle(h6, false);
	        font-size: 14px !important;
            text-transform: lowercase;
            color: #fff;
            background-color: $first-main-color;
            box-sizing: border-box;
            @include qodefTransition(color .2s ease-out, background-color .2s ease-out);
        
            &:hover {
                background-color: $second-main-color;
            }
        
            &:last-child {
                margin-right: 0;
            }
        }
    }
    
    &.widget_media_image {
        margin-bottom: 20px;
	
	    .qodef-widget-title {
		    margin-bottom: 32px;
	    }
        
        a, img {
            display: block;
        }
	    
	    img {
		    
		    &.qodef-image-with-shadow {
			    box-shadow: $additional-box-shadow;
		    }
	    }
    }
    
    &.qodef-blog-list-widget {
    
        .qodef-widget-title {
            margin-bottom: 29px;
        }
    }
}

.qodef-page-header,
.qodef-top-bar,
.qodef-sticky-header,
.qodef-mobile-header {
    
    .widget {
        margin: 0;
        
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_nav_menu,
        &.widget_search {
            display: none !important;
        }
    }
}

.qodef-side-menu {
	text-align: center;
	
	.widget {
		margin-bottom: 15px;
		
		&.widget_media_image {
			margin-bottom: 0;
			
			img {
				margin: 0 auto;
			}
		}
		
		&.widget_archive,
		&.widget_categories,
		&.widget_text {
			
			select {
				max-width: 252px;
			}
		}
		
		&.widget_search {
			
			form {
				max-width: 252px;
			}
		}
	}
}

.qodef-page-footer {
	
	.qodef-icon-widget-holder {
		width: 100%;
		color: #fff;
		
		&:hover {
			color: $first-main-color;
		}
		
		.icon_mail_alt {
			margin-top: -3px;
		}
	}

    .widget {
	    margin-bottom: 25px;
	    color: #b1b2b2;
		
		&.qodef-contact-form-7-widget {
			
			input.wpcf7-form-control.wpcf7-submit {
				width: 100%;
				font-family: $default-text-font;
			}
		}
	    
	    &.qodef-blog-list-widget {
		    
		    .qodef-post-title {
			    color: #a9a9a9;
			    font-weight: 400;
			    
			    a {
				    color: inherit;
				    
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
		
		    .qodef-post-info-date a {
			    color: #fff;
			
			    &:hover {
				    color: $first-main-color;
			    }
		    }
	    }
	
	    &.widget_media_image {
		    margin-bottom: 20px;
	    }
		
		&:last-child {
		    margin: 0 !important;
	    }
	    
        h1, h2, h3, h4, h5, h6 {
            margin: 0;
	        color: #fff;
        }
	
	    .qodef-widget-title {
		    color: #fff;
		    margin: 0 0 23px;
	    }
	
	    a {
		    color: #a9a9a9;
		
		    &:hover {
			    color: $first-main-color;
		    }
	    }
	    
        table thead tr,
        table tbody tr {
            border-color: #b1b2b2;
        }

        table tbody th {
            border-right-color: #b1b2b2;
        }

        /*
        *   Specific styles
        */
	    #wp-calendar {
		
		    caption {
			    color: #b1b2b2;
		    }
	    }
	
	    &.widget_rss {
		
		    .qodef-footer-widget-title {
			
			    .rsswidget {
				    color: #fff;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	    
        &.widget_search {

            form {

                > div {
                    border-color: #b1b2b2;
                }
            }

            input {
                color: #b1b2b2;
	            
	            @include qodefPlaceholder {
		            color: inherit;
	            }

                &[type='submit'] {
                    color: #b1b2b2;
                }
            }

            button {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #b1b2b2;
                border-color: #b1b2b2;

                option {
                    background-color: #363636;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: #b1b2b2;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }
	
	.qodef-footer-bottom-holder {
	
		.widget_text {
			
			a {
				color: #fff;
				
				&:hover {
					color: $first-main-color;
				}
			}
		}
	}
}

.qodef-top-bar {

	a {
		color: #fff;

		&:hover {
			color: $first-main-color;
		}
	}
	
	.qodef-icon-widget-holder {
		color: #fff;
		
		&:hover {
			
			.qodef-icon-text {
				color: $first-main-color;
			}
		}
		
		.qodef-icon-element {
			color: $first-main-color;
		}
		
		.icon_mail_alt {
			margin-top: -3px;
		}
	}

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        p {
            margin: 0;
        }

        &.widget_text {
            margin: 0;
        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }
    }
}
/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */