
#ui-datepicker-div {
    
    width: 223px;
    z-index: 1000 !important; /* datepicker is hidden on z-index lower then 1000 */

    .ui-widget-header {
        background-color: $default-heading-color;
        border-color: $default-heading-color;
        padding: 10px 25px;
        position: relative;

        > * {
            color: #fff;
        }

        .ui-datepicker-prev,
        .ui-datepicker-next {
            cursor: pointer;
	        position: absolute;
	        top: 50%;
	        @include qodefTransform(translateY(-50%));
	        margin-top: -1px;

            &:before {
                display: block;
                font-family: "ElegantIcons";
                font-size: 20px;
                vertical-align: middle;
            }

            .ui-icon {
                display: none;
            }
        }

        .ui-datepicker-prev {
			
            left: 20px;

            &:before {
                content: "\34";
            }
        }

        .ui-datepicker-next {

            right: 20px;

            &:before {
                content: "\35";
            }
        }

        .ui-datepicker-title {
            display: inline-block;
            width: 100%;
            text-align: center;
        }
    }

    .ui-datepicker-calendar {
	    background-color: rgba(#fff, .25);
	    
	    thead {
		    
		    tr {
			    border: none;
			    border-bottom: 1px solid $first-main-color;
		    }
		    
		    th {
			    color: #fff;
			    font-weight: 500;
		    }
	    }
	
	    tbody {
		
		    tr {
			    border: none;
		    }
	    }
        
        td {
            padding: 5px 0;
	        font-size: 12px;
	        color: #fff;
	        
	        a {
		        display: block;
		        margin: 0 auto;
		        width: 25px;
		        height: 25px;
		        line-height: 25px;
		        color: $first-main-color;
		        background-color: #fff;
		        border-radius: 100%;
		        box-sizing: border-box;
	        }
        }
    }
}
